@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,300&display=swap);

body{
  background: url(/portfolio/static/media/background.006fb6bd.png);
  background-size: cover;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}
*{
  outline: none;
}
.home{
  color: black;
  margin-top: -20vh;
  margin-left: 9.5vw;
  font-size: 1.45rem;
  font-weight: bold;
}
#imgHome{
  width: 300px;
  height: 350px;
  border-radius: 50%;
  display: flex;
  margin-left: 68vw;
  margin-top: 20vh;
  justify-content: right;
  box-shadow: 0 0 10px 2px;
}
#imgHome:hover{
  width: 350px;
  height: 430px;
  transition: all 2s;
}
#imgHome:not(hover){
  transition: all 2s;
}
.designation{
  margin-top: -15PX;
  font-weight: bold;
}
.designation:hover{
  transition:1s;
  letter-spacing: 1rem;
}
.name{
  margin-top: -15PX;
  font-weight: bold;
}
.name:hover{
  transition:1s;
  letter-spacing: 1rem;
}
.hi{
  font-weight: bold;
}
.hi:hover{

  transition:1s;
  letter-spacing: 1rem;
}
.front-end{
  font-family: 'Roboto', sans-serif;
  margin-top: -10px;
  letter-spacing: 0.3rem;
  
}
.front-end:hover{
  
  transition:1s;
  letter-spacing: 0.8rem;
}

nav{
  margin: 20px;
  font-size: 1.7rem;
  
}
Link{
  text-decoration: none;
}
.right{
  display: inline;
  margin-left: 58vw;
}

.homeNav{
  margin-left: 20px;
}
.dpbutton{
  background:inherit;
  border-radius: 50%;
  outline: none;
  padding: 6px;
  padding-left: 8px;
  padding-right: 8px;
  
}
.dpbutton:hover{
  color: #ffffffa6;
  transition: all 0.5s;
}

.ABbutton{
  background:inherit;
  outline: none;
  padding: 5px;
  padding-left: 7px;
  padding-right: 7px;
  margin-right: 55px;
}

.ABbutton:hover{
  color: #ffffffa6;
  transition: all 0.5s;
}

.USbutton{
  background:inherit;
  outline: none;
  padding: 5px;
  padding-left: 7px;
  padding-right: 7px;

}

.USbutton:hover{
  color: #ffffffa6;
  transition: all 0.5s;
}
@media screen and (max-width:1500px){
  .ABbutton{
    margin-left: -20px;
    
  }
  .ABbutton{
    margin-right: 25px;
    
  }
  .USbutton{
    margin-right: 5px;
  }

}
#img{
  width: 250px;
  height: 300px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  margin: 70px auto;
  box-shadow: 0 0 10px 2px;
}
#img:hover{
  width: 300px;
  height: 370px;
  transition: all 2s;
}
#img:not(hover){
  transition: all 2s;
}
.nameAbout{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: -55px;
  font-weight: bold;

}
.desig{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: -15px;
  font-weight: bold;
}
.para{
  margin-top: 25px;
  margin-left: 20px;
  margin-right: 20px;
  text-indent: 50px;
  font-size: 17px;
  font-weight: 600;
}
.para2{
  text-indent: 50px;
  margin-left: 20px;
  margin-right: 20px;
  font-size: 17px;
  font-weight: 600;
}
.skills{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30vh;
  font-weight: bold;
}
.lists{
  margin-left: 43vw;
  margin-top: 23px;
  font-size: 17px;
}
#nameid{
  display: inline;
}
.bodyForm{
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
}
.Wrapper{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  padding: 0px 20px;
  margin-top: 45vh;
}
.contact-form{
  max-width: 550px;
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.7);
  padding: 30px;
  border-radius: 5px;
  display: flex;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.53);
}
.input-fields{
  display: flex;
  flex-direction: column;
  margin-right: 4%;
}
.input-fields,.msg{
  width: 48%
}
.input-fields .input, .msg textarea{
  margin: 10px 0;
  background: transparent;
  border: 0;
  border-bottom: 2px solid #c5ecfd;
  padding: 10px;
  color: #c5ecfd;
  width: 100%;
}
.msg textarea{
  height: 166px;
}

::-webkit-input-placeholder{
  color: #c5ecfd;
}
.btn{
  background: #39b7dd;
  text-align: center;
  padding-left: 40%;
  padding-right: 40%;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  outline: none;
  border: 0;
}
.emailMe{
  color: #39b7dd;
}


@media screen and (max-width:600px){
  .contact-form{
    flex-direction: column;
  }
  .msg textarea{
    height: 80px;
  }
  .input-fields,.msg{
    width: 100%
  }
  .btn{
    padding-left: 45.5%;
    padding-right: 45.5%;
  }
}

.call{
  margin: 20px;
  text-align: center;
  justify-content: center;
  font-size: 17px;
  
}
#or{
  font-size: 19px;
}
.Projects{
  width: 80vw;
  height: 70vh;
  background: inherit;
  margin: 90px auto;
  position: relative;
}
.projectimg1, .projectimg2, .projectimg3, .projectimg4,.projectimg5,.projectimg6{
  width: 350px;
  height: 200px;
  border-radius: 35px;
  box-shadow: 0 0 3px 0.9px;

}
.projectimg1:hover,.projectimg2:hover, .projectimg3:hover, .projectimg4:hover,.projectimg5:hover,.projectimg6:hover{
  width: 370px;
  height: 220px;
  transition: all 0.7s;
}
.projectimg1:not( :hover ),.projectimg2:not( :hover ),.projectimg3:not( :hover ),.projectimg4:not( :hover ),.projectimg5:not( :hover ),.projectimg6:not( :hover ){ 
  transition: all 0.7s;
 }
.project1{
  position: absolute;
  margin: 15px;
}
.project2{
  position: absolute;
  left: 428px;
  margin: 15px;
}
.project3{
  position: absolute;
  left: 69%;
  margin: 15px;
}
.project4{
  position: absolute;
  top: 50%;
  margin: 15px;
}
.project5{
  position: absolute;
  top: 50%;
  margin: 15px;
  left: 428px;
}
.project6{
  position: absolute;
  left: 69%;
  top: 50%;
  margin: 15px;
}
.projectName{
  margin-top: 10px;
}
